.bodyDisableScroll {
 overflow: hidden;
}

.Gallery {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-gap: 30px 20px;
}

.Gallery_image {
  width: 100%;
  max-height: 100%;
}

@media (min-width: 768px) {
  .Gallery {
    grid-template-columns: repeat(3, 1fr);
  }
}
